#root {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #e7ffe7;
}

.App {
    width: 90%;
    height: 90vh;
    flex-wrap: wrap;
    justify-content: center;
    align-content: flex-start;
}

.showWords {
    margin-top: 1%;
    display: inline-block;
}

.word {
    margin: 5px 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 15px;
    border: 1px solid #000;
}

.randomWord {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.randomWord__text {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 60px;
    border: 1px solid #000;
    background-color: #ffffff;
    font-size: 20px;
}

.randomWordBtn {
    outline: 0;
    border-color: #175600;
    background: rgb(131, 232, 80);
    border-radius: 5px;
    padding: 5px 15px;
    display: block;
}

.randomWord__downBtns {
    margin: 20px auto ;
    display: flex;
    justify-content: center;
    align-items: center;
}

.randomWord__downBtns--btn {
    margin: 0px 2px;
}


.filterRandom {
    margin-bottom: 5px;
}

.addWords__input {

    outline: 0;
    border-color: #175600;
    background: rgb(255, 255, 255);
    padding: 3px 5px;
}

.addWords__btn {
    outline: 0;
    border-color: #175600;
    background: rgb(131, 232, 80);
    padding: 3px 5px;
}

.addWords__btn--remove {
    background-color: #ffbbbb;
    border-radius: 0px 15px 15px 0px;
}

.filterRandom__btns {
    outline: 0;
    border: 1px solid #175600;
    border-color: #175600;
    background: rgb(131, 232, 80);
    margin: 0;
    margin: 0 1px;
}

.filterRandom__btns:active {
    border-color: #e7ffe7;
}

.refreshBtn {
    border: 1px solid #000;
    border-radius: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    outline: 0;
    background: rgb(131, 232, 80);
    padding: 3px 0;
}

.refreshBtn__img {
    width: 20px;
    height: 20px;
}